(function($, window, document, undefined) {
    function adBannerSlick() {

        $('.adBannerSlick').slick({
            autoplay: true,
            slidesToShow: 1,
            arrows: true,
            infinite: false,
            prevArrow: $('.slick-nav--ad-banner .slick-nav__btn--prev'),
            nextArrow: $('.slick-nav--ad-banner .slick-nav__btn--next'),
            responsive: [{
                breakpoint: 1025,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: true
                }
            }, {
                breakpoint: 769,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: true
                }
            }, {
                breakpoint: 560,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: true
                }
            }]
        });
    }

    var init = function() {
        if ($('#adBannerSlick').length != 0) {
            adBannerSlick()
        }
    };
    $(init);
})(window.jQuery, this, this.document);