  function wordLimitPostLogin(data, limit) {
    var _this = data;
    $(_this).each(function(i) {
      var len = $(this).text().length;
      if (len > limit) {
        $(this).text(
          $(this)
            .text()
            .substr(0, limit) + "…"
        );
      }
    });
  }
  function wordLimitTextNode(data, limit) {
    var _this = data;
    var len = _this.length;
    if (len > limit) {
      return data.substr(0, limit) + "…";
    } else {
      return data;
    }
  }
