(function($, window, document, undefined) {



    function graphSlick() {
        $('.graph-list').each(function(index) {
            $(this).slick({
                autoplay: false,
                slidesToShow: 1,
                arrows: false,
                infinite: false,
                variableWidth: true

            });
        });

    }
    var init = function() {
        if ($('.graph-list').length) {
            graphSlick()
            datePicker('From-date-graph')
            datePicker('to-date-graph')
        }


    };
    $(init);
})(window.jQuery, this, this.document);