(function($, window, document, undefined) {

    function historySlick() {
        $('.history-card-list').each(function(num, elem) {
            elem = $(elem)
            elem.slick({
                autoplay: true,
                slidesToShow: 3,
                arrows: true,
                infinite: false,
                prevArrow: elem.next('.slick-nav--history').find('.slick-nav__btn--prev'),
                nextArrow: elem.next('.slick-nav--history').find('.slick-nav__btn--next'),
                responsive: [{
                    breakpoint: 1025,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 1,
                        arrows: false,
                        dots: true,
                    }
                }, {
                    breakpoint: 768,
                    settings: {
                        slidesToShow: 1.2,
                        slidesToScroll: 1,
                        arrows: false,
                        dots: true,
                    }
                }]
            }).on('setPosition', function (event, slick) {
                slick.$slides.css('height', slick.$slideTrack.height() + 'px');
            });
        });
    }

    function historyYearsSlick() {

        var slidesNo = $('.history-years-list .tabs__link').length - 0.25;
        if (slidesNo > 7) {
            slidesNo = 7
        }
        $('.history-years-list').slick({
            autoplay: false,
            slidesToShow: slidesNo,
            arrows: false,
            infinite: false,
            responsive: [{
                breakpoint: 1025,
                settings: {
                    slidesToShow: 5.5,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: false
                }
            }, {
                breakpoint: 768,
                settings: {
                    slidesToShow: 6,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: false
                }
            }]

        });
    }


    var init = function() {
        historySlick()
        historyYearsSlick()

    };
    $(init);
})(window.jQuery, this, this.document);