(function($, window, document, undefined) {

    function awardSlick() {
        $('.award-list').slick({
            autoplay: true,
            slidesToShow: 3,
            arrows: true,
            infinite: false,
            prevArrow: $('.slick-nav--award .slick-nav__btn--prev'),
            nextArrow: $('.slick-nav--award .slick-nav__btn--next'),
            responsive: [{
                breakpoint: 768,
                settings: {
                    arrows: false,
                    centerMode: true,
                    centerPadding: '0px',
                    slidesToShow: 2.95,
                    dots: true,
                }
            }]
        });

    }

    function testimonialSlick() {
        var slides;

        var slideCount = $('.award-list--testimonials .award').length;
        if (slideCount == 3) {
            slides = 2.35;
        } else if (slideCount > 3) {
            slides = 3;
        }


        $('.award-list--testimonials').slick({
            centerMode: true,
            centerPadding: '30px',
            autoplay: true,
            slidesToShow: slides,
            arrows: true,
            infinite: true,
            prevArrow: $('.slick-nav--award-testi .slick-nav__btn--prev'),
            nextArrow: $('.slick-nav--award-testi .slick-nav__btn--next'),
            responsive: [{
                breakpoint: 768,
                settings: {
                    arrows: false,
                    centerMode: true,
                    centerPadding: '0px',
                    slidesToShow: 2.98,
                    dots: true,
                    infinite: true,
                }
            }]
        });
    }

    var init = function() {

        if ($('.award-list').hasClass('award-list--testimonials')) {
            testimonialSlick();
        } else {
            awardSlick();
        }

    };
    $(init);
})(window.jQuery, this, this.document);