(function($, window, document, undefined) {


    function infoCardSlick() {
        $('.info-card__wrap--typ1').slick({
            autoplay: true,
            slidesToShow: 3,
            arrows: true,
            infinite: false,
            prevArrow: $('.slick-nav--info-card .slick-nav__btn--prev'),
            nextArrow: $('.slick-nav--info-card .slick-nav__btn--next'),
            responsive: [{
                breakpoint: 1270,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: false
                }
            }, {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: true
                }
            }, {
                breakpoint: 768,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: true
                }
            }]
        }).on('setPosition', function (event, slick) {
            slick.$slides.css('height', slick.$slideTrack.height() + 'px');
        });

        $('.info-card__wrap--typ2').slick({
            autoplay: true,
            slidesToShow: 3,
            arrows: true,
            infinite: false,
            prevArrow: $('.slick-nav--info-card2 .slick-nav__btn--prev'),
            nextArrow: $('.slick-nav--info-card2 .slick-nav__btn--next'),
            responsive: [{
                breakpoint: 1270,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: false
                }
            }, {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: true
                }
            }, {
                breakpoint: 768,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: true
                }
            }]
        }).on('setPosition', function (event, slick) {
            slick.$slides.css('height', slick.$slideTrack.height() + 'px');
        });

        $('.info-card__wrap--typ3').slick({
            autoplay: true,
            slidesToShow: 2,
            arrows: true,
            infinite: false,
            prevArrow: $('.slick-nav--info-card .slick-nav__btn--prev'),
            nextArrow: $('.slick-nav--info-card .slick-nav__btn--next'),
            responsive: [{
                breakpoint: 1270,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: false
                }
            }, {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: true
                }
            }, {
                breakpoint: 768,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: true
                }
            }]
        }).on('setPosition', function (event, slick) {
            slick.$slides.css('height', slick.$slideTrack.height() + 'px');
        });


    }

    var init = function() {
        infoCardSlick()
    };
    $(init);
})(window.jQuery, this, this.document);