(function($, window, document, undefined) {

    function articleSlick() {
        /* ---------- AllArticles  --------- */

        $('#allArticles').slick({
            autoplay: true,
            slidesToShow: 3,
            arrows: true,
            infinite: true,
            prevArrow: $('.article-list__slick-nav--all .slick-nav__btn--prev'),
            nextArrow: $('.article-list__slick-nav--all .slick-nav__btn--next'),
            responsive: [{
                breakpoint: 1024,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    dots: true,
                }
            }, {
                breakpoint: 768,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    dots: true,
                }
            }]
        }).on('setPosition', function(event, slick) {
            slick.$slides.css('height', slick.$slideTrack.height() + 'px');
        });

        /* ---------- popularArticles  --------- */
        $('#popularArticles').slick({
            autoplay: true,
            slidesToShow: 3,
            arrows: true,
            infinite: true,
            prevArrow: $('.article-list__slick-nav--popular .slick-nav__btn--prev'),
            nextArrow: $('.article-list__slick-nav--popular .slick-nav__btn--next'),
            responsive: [{
                breakpoint: 1024,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    dots: true,
                }
            }, {
                breakpoint: 768,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    dots: true,
                }
            }]
        }).on('setPosition', function(event, slick) {
            slick.$slides.css('height', slick.$slideTrack.height() + 'px');
        });
        
        /* ---------- likedArticles  --------- */
        $('#likedArticles').slick({
            autoplay: true,
            slidesToShow: 3,
            arrows: true,
            infinite: true,
            prevArrow: $('.article-list__slick-nav--liked-article .slick-nav__btn--prev'),
            nextArrow: $('.article-list__slick-nav--liked-article .slick-nav__btn--next'),
            responsive: [{
                breakpoint: 1024,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    dots: true,
                }
            }, {
                breakpoint: 768,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    dots: true,
                }
            }]
        }).on('setPosition', function(event, slick) {
            slick.$slides.css('height', slick.$slideTrack.height() + 'px');
        });        

        /* ---------- recentlyViewedArticles  --------- */
        $('#recentlyViewedArticles').slick({
            autoplay: true,
            slidesToShow: 3,
            arrows: true,
            infinite: false,
            prevArrow: $('.article-list__slick-nav--recently-view .slick-nav__btn--prev'),
            nextArrow: $('.article-list__slick-nav--recently-view .slick-nav__btn--next'),
            responsive: [{
                breakpoint: 1024,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    dots: true,
                }
            }, {
                breakpoint: 768,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    dots: true,
                }
            }]
        }).on('setPosition', function(event, slick) {
            slick.$slides.css('height', slick.$slideTrack.height() + 'px');
        });

        /* ---------- trendingArticles  --------- */
        $('#trendingArticles').slick({
            autoplay: true,
            slidesToShow: 3,
            arrows: true,
            infinite: true,
            responsive: [{
                breakpoint: 1024,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    dots: true,
                }
            }, {
                breakpoint: 768,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    dots: true,
                }
            }]
        }).on('setPosition', function(event, slick) {
            slick.$slides.css('height', slick.$slideTrack.height() + 'px');
        });        

    }



    var init = function() {
        articleSlick()
    };
    $(init);
})(window.jQuery, this, this.document);